"use client";

import React, { useEffect, useState, useCallback } from 'react';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import Select from '@/components/ui/Select';
import DatePicker from '@/components/form/date-picker';
import { Empresa, UpdateEmpresaRequest } from '@/services/api/empresa.service';
import { toast } from 'react-hot-toast';
import axios from 'axios';
import { useAuth } from '@/hooks/useAuth';

type EmpresaFormProps = {
  empresa?: Empresa | null;
  onSubmit: (data: UpdateEmpresaRequest) => Promise<void>;
  isSubmitting?: boolean;
};

type FormState = UpdateEmpresaRequest;

const estados = [
  { value: 'AC', label: 'Acre' },
  { value: 'AL', label: 'Alagoas' },
  { value: 'AP', label: 'Amapá' },
  { value: 'AM', label: 'Amazonas' },
  { value: 'BA', label: 'Bahia' },
  { value: 'CE', label: 'Ceará' },
  { value: 'DF', label: 'Distrito Federal' },
  { value: 'ES', label: 'Espírito Santo' },
  { value: 'GO', label: 'Goiás' },
  { value: 'MA', label: 'Maranhão' },
  { value: 'MT', label: 'Mato Grosso' },
  { value: 'MS', label: 'Mato Grosso do Sul' },
  { value: 'MG', label: 'Minas Gerais' },
  { value: 'PA', label: 'Pará' },
  { value: 'PB', label: 'Paraíba' },
  { value: 'PR', label: 'Paraná' },
  { value: 'PE', label: 'Pernambuco' },
  { value: 'PI', label: 'Piauí' },
  { value: 'RJ', label: 'Rio de Janeiro' },
  { value: 'RN', label: 'Rio Grande do Norte' },
  { value: 'RS', label: 'Rio Grande do Sul' },
  { value: 'RO', label: 'Rondônia' },
  { value: 'RR', label: 'Roraima' },
  { value: 'SC', label: 'Santa Catarina' },
  { value: 'SP', label: 'São Paulo' },
  { value: 'SE', label: 'Sergipe' },
  { value: 'TO', label: 'Tocantins' },
];

export default function EmpresaForm({ empresa, onSubmit, isSubmitting }: EmpresaFormProps) {
  const { user } = useAuth();
  const [form, setForm] = useState<FormState>({
    nome: empresa?.nome ?? '',
    cpfCnpj: empresa?.cpfCnpj ?? '',
    celular: empresa?.celular ?? '',
    cep: empresa?.cep ?? '',
    cidade: empresa?.cidade ?? '',
    estado: empresa?.estado ?? '',
    bairro: empresa?.bairro ?? '',
    rua: empresa?.rua ?? '',
    numero: empresa?.numero ?? '',
    // vencimento é opcional (apenas SUPERADMIN altera). Vamos inicializar como undefined para não mudar por engano
  });

  useEffect(() => {
    setForm({
      nome: empresa?.nome ?? '',
      cpfCnpj: empresa?.cpfCnpj ?? '',
      celular: empresa?.celular ?? '',
      cep: empresa?.cep ?? '',
      cidade: empresa?.cidade ?? '',
      estado: empresa?.estado ?? '',
      bairro: empresa?.bairro ?? '',
      rua: empresa?.rua ?? '',
      numero: empresa?.numero ?? '',
    });
  }, [empresa]);

  const [errosCep, setErrosCep] = useState(0);
  const handleCepBlur = useCallback(async (newCep: string, url: string = 'https://viacep.com.br/ws/{cep}/json/') => {
    const cep = (newCep || '').replace(/\D/g, '');
    if (cep.length !== 8) return;
    console.log('cep.length', cep.length, 'cep', cep);
    try {
      // const url = `https://brasilapi.com.br/api/cep/v1/${cep}`;
      // const url = `https://viacep.com.br/ws/${cep}/json/`;
      const { data } = await axios.get(url.replace('{cep}', cep));
      if (data?.erro) {
        console.error('CEP não encontrado, erro:', data.erro);
        setErrosCep(prev => prev + 1);
        return;
      };
      setForm(prev => ({
        ...prev,
        rua: data.street || data.logradouro || prev.rua,
        bairro: data.neighborhood || data.bairro || prev.bairro,
        cidade: data.city || data.localidade || prev.cidade,
        estado: data.state || data.uf || prev.estado,
      }));
      setErrosCep(0);
    } catch (e) {
      // silencioso
      console.error('Erro ao buscar CEP:', e);
      setErrosCep(prev => prev + 1);

    }
  }, []);

  useEffect(() => {
    if (errosCep === 0) return;
    if (errosCep > 1) {
      console.log('Muitos erros ao buscar CEP, não tentando mais');
      setErrosCep(0);
      return;
    }; // evita muitas tentativas ou desnecessárias
    const timer = setTimeout(() => {
      handleCepBlur(form.cep || '', 'https://brasilapi.com.br/api/cep/v1/{cep}');
    }, 3000);
    return () => clearTimeout(timer);
  }, [errosCep, form.cep, handleCepBlur]);
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!form.nome) {
      toast.error('Nome é obrigatório');
      return;
    }
    await onSubmit(form);
  };

  const canEditVencimento = user?.role === 'SUPERADMIN';

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <Label htmlFor="nome">Nome</Label>
          <Input id="nome" value={form.nome || ''} onChange={(e) => setForm({ ...form, nome: e.target.value })} required />
        </div>
        <div>
          <Label htmlFor="cpfCnpj">CPF/CNPJ</Label>
          <Input id="cpfCnpj" value={form.cpfCnpj || ''} onChange={(e) => setForm({ ...form, cpfCnpj: e.target.value })} />
        </div>
        <div>
          <Label htmlFor="celular">Celular</Label>
          <Input id="celular" value={form.celular || ''} onChange={(e) => setForm({ ...form, celular: e.target.value })} />
        </div>
        <div>
          <Label htmlFor="cep">CEP</Label>
          <Input id="cep" value={form.cep || ''} onChange={(e) => {
            setForm({ ...form, cep: e.target.value })
            handleCepBlur(
              e.target.value
            )
          }} />
        </div>
        <div>
          <Label htmlFor="rua">Rua</Label>
          <Input id="rua" value={form.rua || ''} onChange={(e) => setForm({ ...form, rua: e.target.value })} />
        </div>
        <div>
          <Label htmlFor="numero">Número</Label>
          <Input id="numero" value={form.numero || ''} onChange={(e) => setForm({ ...form, numero: e.target.value })} />
        </div>
        <div>
          <Label htmlFor="bairro">Bairro</Label>
          <Input id="bairro" value={form.bairro || ''} onChange={(e) => setForm({ ...form, bairro: e.target.value })} />
        </div>
        <div>
          <Label htmlFor="cidade">Cidade</Label>
          <Input id="cidade" value={form.cidade || ''} onChange={(e) => setForm({ ...form, cidade: e.target.value })} />
        </div>
        <div>
          <Label htmlFor="estado">Estado</Label>
          <Select
            id="estado"
            options={estados}
            value={estados.find(e => e.value === form.estado)}
            onChange={(opt) => setForm({ ...form, estado: (opt as { value: string })?.value })}
            isClearable
          />
        </div>

        <div className={canEditVencimento ? '' : 'opacity-50 pointer-events-none'}>
          <Label htmlFor="vencimento">Vencimento {/*canEditVencimento ? '' : '(apenas SUPERADMIN pode editar)'*/}</Label>
          <DatePicker
            id="vencimento"
            defaultDate={empresa?.vencimento ? new Date(empresa.vencimento) : undefined}
            onChange={(dates: Date[]) => {
              const d = dates?.[0];
              setForm(prev => ({ ...prev, vencimento: d as any }));
            }}
          />
        </div>
      </div>

      <div className="flex gap-3 pt-6">
        <Button type="submit" variant="primary" disabled={!!isSubmitting} className="min-w-40">
          Salvar alterações
        </Button>
      </div>
    </form>
  );
}
