"use client";

import React, { useEffect, useState, useCallback } from 'react';
import { toast } from 'react-hot-toast';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import { empresaService, Empresa, UpdateEmpresaRequest } from '@/services/api/empresa.service';
import EmpresaForm from '@/components/empresa/EmpresaForm';
import { useAuth } from '@/hooks/useAuth';

export default function ConfigEmpresaPage() {
  const { user } = useAuth();
  const [empresa, setEmpresa] = useState<Empresa | null>(null);
  const [loading, setLoading] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Assumimos uma única empresa por usuário logado (ADMIN/GESTOR). Se houver multi-empresa, adaptar para selecionar ID.
  const loadEmpresa = useCallback(async () => {
    try {
      setLoading(true);
      // Pega a empresa do usuário autenticado
      const id = user?.empresaId || user?.empresa?.id;
      if (!id) {
        setEmpresa(null);
        return;
      }
      const detail = await empresaService.getById(id);
      setEmpresa(detail.data);
    } catch (error) {
      toast.error('Erro ao carregar dados da empresa');
    } finally {
      setLoading(false);
    }
  }, [user]);

  useEffect(() => {
    loadEmpresa();
  }, [loadEmpresa]);

  const handleSave = async (data: UpdateEmpresaRequest) => {
    if (!empresa) return;
    try {
      setIsSubmitting(true);
      const payload: UpdateEmpresaRequest = { ...data };
      // Se não for SUPERADMIN, não envie vencimento (back pode validar também)
      if (user?.role !== 'SUPERADMIN') {
        delete (payload as any).vencimento;
      }
      await empresaService.update(empresa.id, payload);
      toast.success('Configurações atualizadas');
      await loadEmpresa();
    } catch (error) {
      toast.error('Erro ao salvar');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-1">
              Configurações da Empresa
            </h1>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Atualize os dados cadastrais e preferências da sua empresa
            </p>
          </div>
        </div>
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : empresa ? (
        <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6">
          <EmpresaForm empresa={empresa} onSubmit={handleSave} isSubmitting={isSubmitting} />
        </div>
      ) : (
        <div className="text-center text-gray-500 dark:text-gray-400">
          Nenhuma empresa encontrada.
        </div>
      )}
    </div>
  );
}
